import express from 'express';
import expressJWT from 'express-jwt';
import env from '../../../config/env';
import getLoggedInUser from '../../../middlewares/get_loggedIn_user';
import updateBreakages from '../controllers/breakages_put';
import { baseUrl } from '../baseurl';

const router = express.Router();

/**
 * @swagger
 * /breakages:
 *   put:
 *     tags:
 *       - Breakages
 *     description: Returns success message if breakage details are updated
 *     consumes:
 *       - application/json
 *     produces:
 *       - application/json
 *     parameters:
 *       - in: "body"
 *         name: "body"
 *         description: "Required details"
 *         required: true
 *         schema:
 *          type: array
 *          items:
 *            type: object
 *            properties:
 *              _id:
 *                type: string
 *              update:
 *                type: object
 *                properties:
 *                  productCode:
 *                    type: string
 *                  brandCode:
 *                    type: string
 *                  productName:
 *                    type: string
 *                  sizeCode:
 *                    type: string
 *                  date:
 *                    type: date
 *     responses:
 *       200:
 *         description: A JSON object
 *         schema:
 *           type: object
 *           properties:
 *            message:
 *              type: string
 */

router.put(
  `${baseUrl}/`,
  expressJWT({
    secret: env.JWT_SECRET,
  }),
  getLoggedInUser,
  updateBreakages
);

export default router;
